:root {
  --bg-primary: #0a0a0b;
  --bg-secondary: #111113;
  --text-primary: #e8e6e3;
  --text-muted: #8a8885;
  --accent: #c9a55c;
  --accent-dim: #8b7355;
  --border: #2a2a2d;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 18px;
}
body {
  font-family: var(--font-cormorant), Georgia, serif;
  background: var(--bg-primary);
  color: var(--text-primary);
  min-height: 100vh;
  line-height: 1.6;
  overflow-x: hidden;
}
body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 256 256' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noise'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noise)'/%3E%3C/svg%3E");
  opacity: 0.03;
  pointer-events: none;
  z-index: 1000;
}
.grid-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(var(--border) 1px, transparent 1px),
    linear-gradient(90deg, var(--border) 1px, transparent 1px);
  background-size: 60px 60px;
  opacity: 0.15;
  animation: gridMove 60s linear infinite;
}
@keyframes gridMove {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(60px, 60px);
  }
}
.container {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 2rem;
  z-index: 1;
}
header {
  padding: 2rem 0;
  border-bottom: 1px solid var(--border);
}
.logo {
  font-size: 0.85rem;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: var(--text-muted);
  font-family: 'JetBrains Mono', monospace;
}
.hero {
  padding: 8rem 0 6rem;
  text-align: center;
}
.hero-tag {
  display: inline-block;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.7rem;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: var(--accent);
  border: 1px solid var(--accent-dim);
  padding: 0.5rem 1rem;
  margin-bottom: 2rem;
  animation: fadeInUp 0.8s ease-out;
}
h1 {
  font-size: clamp(2.5rem, 8vw, 4.5rem);
  font-weight: 400;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  animation: fadeInUp 0.8s ease-out 0.1s both;
}
h1 em {
  font-style: italic;
  color: var(--accent);
}
.subtitle {
  font-size: 1.25rem;
  color: var(--text-muted);
  max-width: 540px;
  margin: 0 auto 3rem;
  animation: fadeInUp 0.8s ease-out 0.2s both;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.stats {
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding: 3rem 0;
  border-top: 1px solid var(--border);
  border-bottom: 1px solid var(--border);
  margin: 4rem 0;
  animation: fadeInUp 0.8s ease-out 0.3s both;
}
.stat {
  text-align: center;
}
.stat-value {
  font-family: 'JetBrains Mono', monospace;
  font-size: 2rem;
  color: var(--accent);
  display: block;
}
.stat-label {
  font-size: 0.8rem;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-top: 0.5rem;
}
.preview {
  padding: 4rem 0;
  animation: fadeInUp 0.8s ease-out 0.4s both;
}
.preview h2 {
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--text-muted);
  margin-bottom: 2rem;
}
.discovery-list {
  list-style: none;
}
.discovery-list li {
  padding: 1.5rem 0;
  border-bottom: 1px solid var(--border);
  display: flex;
  gap: 1.5rem;
  align-items: baseline;
}
.discovery-num {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.75rem;
  color: var(--accent-dim);
}
.discovery-text {
  font-size: 1.2rem;
}
.cta-section {
  padding: 5rem 0;
  text-align: center;
  animation: fadeInUp 0.8s ease-out 0.5s both;
}
.cta-section p {
  font-size: 1.1rem;
  color: var(--text-muted);
  margin-bottom: 2rem;
}
.email-form {
  display: flex;
  gap: 1rem;
  max-width: 400px;
  margin: 0 auto;
  flex-wrap: wrap;
  justify-content: center;
}
.email-input {
  flex: 1;
  min-width: 200px;
  padding: 1rem 1.25rem;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.85rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  color: var(--text-primary);
  outline: none;
  transition: border-color 0.3s;
}
.email-input:focus {
  border-color: var(--accent-dim);
}
.email-input::placeholder {
  color: var(--text-muted);
}
.submit-btn {
  padding: 1rem 2rem;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.75rem;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background: var(--accent);
  color: var(--bg-primary);
  border: none;
  cursor: pointer;
  transition: all 0.3s;
}
.submit-btn:hover {
  background: var(--text-primary);
}
footer {
  padding: 3rem 0;
  border-top: 1px solid var(--border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}
.footer-links {
  display: flex;
  gap: 2rem;
}
.footer-links a {
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.75rem;
  color: var(--text-muted);
  text-decoration: none;
  letter-spacing: 0.05em;
  transition: color 0.3s;
}
.footer-links a:hover {
  color: var(--accent);
}
.copyright {
  font-size: 0.8rem;
  color: var(--text-muted);
}
.float-decor {
  position: fixed;
  right: 5%;
  top: 20%;
  width: 300px;
  height: 300px;
  border: 1px solid var(--border);
  border-radius: 50%;
  opacity: 0.3;
  animation: float 20s ease-in-out infinite;
}
.float-decor::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60%;
  height: 60%;
  border: 1px solid var(--accent-dim);
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
@keyframes float {
  0%, 100% {
    transform: translateY(0) rotate(0deg);
  }
  50% {
    transform: translateY(-30px) rotate(180deg);
  }
}
@media (max-width: 600px) {
  .stats {
    flex-direction: column;
    gap: 2rem;
  }
  .float-decor {
    display: none;
  }
  footer {
    flex-direction: column;
    text-align: center;
  }
}
.research-article {
  max-width: 720px;
  margin: 0 auto;
  padding: 2rem 0 4rem;
}
.article-header {
  margin-bottom: 3rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid var(--border);
}
.article-tag {
  font-family: var(--font-jetbrains), monospace;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  color: var(--accent);
  display: block;
  margin-bottom: 1rem;
}
.article-header h1 {
  font-size: 2.2rem;
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: 1rem;
}
.article-meta {
  font-size: 0.9rem;
  color: var(--text-muted);
}
.article-section {
  margin-bottom: 3rem;
}
.article-section h2 {
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
}
.article-section p {
  margin-bottom: 1.25rem;
  line-height: 1.7;
  color: var(--text-primary);
}
.article-section a {
  color: var(--accent);
  text-decoration: underline;
  text-underline-offset: 2px;
}
.article-section a:hover {
  color: var(--text-primary);
}
.lead {
  font-size: 1.15rem;
  line-height: 1.8;
  color: var(--text-primary);
}
.caveat {
  background: var(--bg-secondary);
  border-left: 3px solid var(--accent-dim);
  padding: 1rem 1.25rem;
  border-radius: 0 4px 4px 0;
  font-size: 0.9rem;
  color: var(--text-muted);
}
.caveat strong {
  color: var(--text-primary);
}
.data-summary {
  display: flex;
  justify-content: space-around;
  padding: 2rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border);
  border-radius: 4px;
  margin: 2rem 0;
}
.data-item {
  text-align: center;
}
.data-item .data-value {
  display: block;
  font-family: var(--font-jetbrains), monospace;
  font-size: 2rem;
  color: var(--accent);
}
.data-item .data-label {
  font-size: 0.8rem;
  color: var(--text-muted);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
.limitations-list {
  list-style: none;
  padding: 0;
  margin: 1.5rem 0;
}
.limitations-list li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 1rem;
  line-height: 1.6;
  color: var(--text-muted);
}
.limitations-list li::before {
  content: "â€”";
  position: absolute;
  left: 0;
  color: var(--accent-dim);
}
.limitations-list strong {
  color: var(--text-primary);
}
.article-footer {
  margin-top: 4rem;
  padding-top: 2rem;
  border-top: 1px solid var(--border);
}
.footer-nav {
  display: flex;
  gap: 2rem;
  margin-bottom: 1.5rem;
}
.footer-nav a {
  font-family: var(--font-jetbrains), monospace;
  font-size: 0.85rem;
  color: var(--accent);
  text-decoration: none;
  padding: 0.5rem 1rem;
  border: 1px solid var(--accent-dim);
  border-radius: 4px;
  transition: all 0.2s;
}
.footer-nav a:hover {
  background: var(--accent);
  color: var(--bg-primary);
  border-color: var(--accent);
}
.footer-note {
  font-size: 0.85rem;
  color: var(--text-muted);
}
.research-cta {
  text-align: center;
  padding: 3rem 0;
  margin: 2rem 0;
}
.research-cta p {
  color: var(--text-muted);
  margin-bottom: 1rem;
}
.research-link {
  font-family: var(--font-jetbrains), monospace;
  font-size: 0.9rem;
  color: var(--accent);
  text-decoration: none;
  padding: 0.75rem 1.5rem;
  border: 1px solid var(--accent);
  border-radius: 4px;
  transition: all 0.2s;
  display: inline-block;
}
.research-link:hover {
  background: var(--accent);
  color: var(--bg-primary);
}
@media (max-width: 768px) {
  .research-article {
    padding: 1rem;
  }
  .article-header h1 {
    font-size: 1.75rem;
  }
  .data-summary {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer-nav {
    flex-direction: column;
    gap: 1rem;
  }
  .footer-nav a {
    text-align: center;
  }
}
